body.active-modal {
    overflow-y: hidden;
}

.project-containeur{
    background-color: $black-1;
    height: 100vh;
    display: grid;
    grid-template-columns: 30% 70%;
    align-items: center;
    font-family: $font-1;

    .zoom-here {
        height: 150px;
        position: absolute;
        color: $color-3;
        font-weight: 700;
        font-size: 1.2rem;
        left: 22%;
        top: 180px;
        transform: rotate(25deg);
            @media screen and (max-width: 1250px) {
                display: none;
            }
    }

    @media screen and (max-width: 900px) {
        grid-template-columns: 1fr;
      }

    
    .containeur-left {
        @include verticalCenter;
        flex-direction: column;
        color: $white-1;

            .description {
                font-size: 1.15rem;
                text-align: center;
            }

            .description-p {
                font-size: 0.9rem;
                text-align: center;
                margin: 15px 0 30px;
                max-width: 70%;
                color: $color-3;
            }

            ul {
                font-size: 0.75rem;
                display: flex;
                margin-top: 30px;

                li {
                    background-color: rgba(45,212,191,.1);
                    border-radius: 15px;
                    font-weight: 500;
                    // color: $color-1;
                    color : $color-3;
                    margin-right: 0.375rem;
                    padding: 0.25rem 0.75rem;
                }
            }

            .code-btn {

                margin-top: 100px;
                cursor: pointer;
                transition: all .3s ease;

                @media screen and (max-width: 900px) {
                    margin-top: 50px;
                  }
                
                a {
                    color: $white-1;
                }

                .arrow-btn {
                    transition: all .3s ease;
                    height: 14px;
                }
                &:hover .arrow-btn {
                    transform: rotate(90deg);
                }
            }
        
        @media screen and (max-width: 900px) {
                margin-top: 50px;
        }

    
    }


    .containeur-right {
        
        @include verticalCenter;

        img {
            width: 90%;
            margin: 0 auto;
            box-shadow: 12px 12px 100px $white-1, -12px -12px 100px $white-1;
            cursor: zoom-in;

            @media screen and (max-width: 900px) {
                box-shadow: 6px 6px 50px $white-1, -6px -6px 50px $white-1;
            }
        }

        #no-modal {
            cursor: auto;
        }

        @media screen and (max-width: 900px) {
            margin-top: -150px;
          }
        @media screen and (max-width: 440px) {
            display: none;
          }

    }

        .modal{
            position: absolute;
            width: 100%;
            align-self: center;
            height: 100%;
            overflow-y: scroll;
            top: auto;
            left: auto;
            right: auto;
            bottom: auto;

            
            .modal-content {
                
                img {
                    vertical-align: middle;
                    display: inline-block;
                    max-height: 100%;
                    max-width: 100%;
            }
            }
            .close-modal {
             position: fixed;
             top: 10px;
             right: 30px;
             padding: 8px 22px;
             filter: contrast(200%);
             background: rgba(0, 0, 0, 0.3);
             color: $white-1;
             border: none;
             font-size: 1.15rem;
             font-weight: 800;
             cursor: pointer;
          }
    }
}